import { component, html, list } from '../../dist/src';
import { key } from '../../dist/src/dom/directives/list';
function createItem() {
  return {
    label: 'todo item',
    done: false,
    id: `item-${Date.now()}`,
  };
}
component('todo-app', function*(state) {
  state.items = [createItem()];
  for (;;) {
    yield () => {
      const { items = [] } = state;
      return html`
        <ul>
          ${list(
            state.items
              .filter(item => !item.done)
              .map(
                item => html`
                  <todo-item
                    ${key(item.id)}
                    label="${item.label}"
                    .done="${item.done}"
                  ></todo-item>
                `
              )
          )}
        </ul>
      `;
    };
  }
});
